import { useSelector } from "react-redux";
import { Routes, Route, Link, useNavigate, Outlet } from "react-router-dom";

export default function AllPerfume() {
  let state = useSelector((state) => {
    return state;
  });
  return (
    <div>
      <section className="products-container">
        <h2>PERFUME</h2>
        {state.perfume.map((a, i) => {
          return (
            <Link to={`/perfume/${a.id}`} key={a.id}>
              <div key={i} className="product-card">
                <img src={`${a.img}`} width="30%" className="product-image" />
                <div className="product-txt-container">
                  <p className="product-name">{a.name}</p>
                  <p className="product-price">
                    {Number(a.price).toLocaleString()}Ïõê
                  </p>
                </div>
              </div>
            </Link>
          );
        })}
      </section>
    </div>
  );
}
